.snake_move {
  width: 900px;
  height: 450px;
  float: right;
  position: fixed;
  animation: myfirst_a 5s infinite linear;
  -webkit-animation: myfirst_a 5s infinite linear;
  /* Safari and Chrome */ }

@keyframes myfirst_a {
  0% {
    left: 1000px;
    top: 25%; }
  50% {
    left: 500px;
    top: 25%; }
  100% {
    left: 1000px;
    top: 25%; } }

@-webkit-keyframes myfirst_a {
  0% {
    left: 1000px;
    top: 25%; }
  50% {
    left: 500px;
    top: 25%; }
  100% {
    left: 1000px;
    top: 25%; } }

.progress-box {
  display: inline-block;
  margin-left: 20px; }

.progress-circle {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 9px solid white;
  vertical-align: middle;
  display: inline-block;
  position: relative;
  z-index: 99; }

.progress-loading {
  width: 94px;
  height: 32px;
  border-radius: 16px;
  margin-left: -27px;
  background: white;
  vertical-align: middle;
  display: inline-block;
  position: relative;
  z-index: 1; }

.progress-loading-in {
  height: 30px;
  width: 0px;
  boder-width: 1px 0px;
  border-color: white;
  border-style: solid;
  border-radius: 16px; }

.progress-loading-in-twinkling {
  height: 30px;
  width: 0px;
  boder-width: 1px 0px;
  border-color: white;
  border-style: solid;
  border-radius: 16px;
  animation: twinkling 0.5s infinite linear;
  -webkit-animation: twinkling 0.5s infinite linear;
  /* Safari and Chrome */ }

@keyframes twinkling {
  0% {
    opacity: 0.1; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0.1; } }

@-webkit-keyframes twinkling {
  0% {
    opacity: 0.1; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0.1; } }

